import React, { Component } from 'react'
import './App.css'
import { loadExtendscript, evalExtendscript } from 'cep-interface'
import RadialInput from '../RadialInput/RadialInput'
import NumberInput from '../NumberInput/NumberInput'
import NumberUnitInput from '../NumberUnitInput/NumberUnitInput'
import DropdownSelect from '../DropdownSelect/DropdownSelect'
import { removeFromArray, addToArray, increaseValue } from '../util/state-helper';
import { debounce } from '../util/input'
import ValueDragger from '../ValueDragger/ValueDragger'

class App extends Component {

  startFromOptions = ['Selection', 'Upper Layer']
  tabs = ['Shadow', 'Blur']

  shadowSettings = {
    presetName: '',
    angle: 0,
    distance: { value: 40, unit: 'px' },
    steps: 20,
    stepScale: 1.3,
    blur: 40,
    useGlobalLight: false,
    flatten: true,
  }

  blurSettings = {
    presetName: '',
    angle: 0,
    steps: 15,
    stepScale: 1.5,
    gaussianBlur: 50,
    motionBlur: 50,
    startFrom: this.startFromOptions[0]
  }

  initialState = {
    shadowSettings: this.shadowSettings,
    blurSettings: this.blurSettings,
    blurPresets: [],
    shadowPresets: [],
    activeTab: 0,
    justSaved: false
  }

  state = {
    ...this.initialState
  }

  constructor() {
    super()
    if (window.__adobe_cep__ !== undefined) {
      loadExtendscript('polyfill.jsx')
      loadExtendscript('json2.js')
      loadExtendscript('ps-utility.jsx')
      loadExtendscript('index.jsx')

      evalExtendscript('getLastSettings()').then(rememberedState => {
        if (rememberedState) {
          this.setState({ ...rememberedState })
        }
      })

      evalExtendscript('getBlurPresets()').then(presets => {
        if (presets) {
          this.setState({ blurPresets: presets.split(',') })
        }
      })

      evalExtendscript('getShadowPresets()').then(presets => {
        if (presets) {
          this.setState({ shadowPresets: presets.split(',') })
        }
      })
    }
  }

  render() {
    return (
      <div className="App">
        <header>
          <div className="logo">
            <img src="./assets/logo_icon.svg" alt="logo" className="icon"></img>
            <img src="./assets/logo_typo.svg" className="typo" alt="logo"></img>
          </div>
        </header>
        <main>

          <div className="tabs">
            {this.tabs.map((tab, index) => (
              <div key={index}
                onClick={() => this.setState({ activeTab: index })}
                className={'tab ' + (this.state.activeTab === index ? 'active' : '')}
              >{tab}</div>
            ))}
          </div>

          {this.state.activeTab === 0 ?
            this.renderShadowSettings() : this.renderBlurSettings()
          }


        </main>
      </div>
    );
  }

  renderBlurSettings() {
    return <section className="tabContent">
      <div className="presetArea row">
        <div className="heading">Save / Load Preset</div>
        <div className="grid settings gap v-center row">
          <input type="text" className="presetName"
            placeholder="Preset Name"
            onChange={e => this.updateBlurSetting('presetName', e.target.value)}
            value={this.state.blurSettings.presetName} />
          <button onClick={this.saveBlurPreset}
            className={this.state.justSaved ? 'justSaved' : ''}>
            {this.state.blurPresets.includes(this.state.blurSettings.presetName) ? 'Update' : 'Save'}
          </button>
        </div>
        <DropdownSelect hasAction ellipsis
          displayText="Load Preset"
          noOptionsText="no presets saved"
          options={this.state.blurPresets}
          onAction={this.deleteBlurPreset}
          onSelect={this.loadBlurPreset}>
        </DropdownSelect>
      </div>
      <div className="heading">Adjust Settings</div>
      <div className="grid settings gap v-center row">
        <div className="flex angle-tuple">
          <ValueDragger deltaChange={delta => this.increaseSetting('angle', delta, true, -180, 180)}>
            <label>Angle</label>
          </ValueDragger>
          <RadialInput value={this.state.blurSettings.angle}
            onChange={val => this.updateBlurSetting('angle', val)}>
          </RadialInput>
        </div>
        <NumberInput onChange={val => this.updateBlurSetting('angle', val)}
          value={this.state.blurSettings.angle} min={-180} max={180} suffix="deg"></NumberInput>

        <label>Start From</label>
        <DropdownSelect
          onSelect={opt => this.updateBlurSetting('startFrom', opt)}
          displayText={this.state.blurSettings.startFrom}
          selectedOption={this.state.blurSettings.startFrom}
          options={this.startFromOptions}>
        </DropdownSelect>

        <ValueDragger deltaChange={delta => this.increaseSetting('steps', delta, true, 1, 9999)}>
          <label>Steps</label>
        </ValueDragger>
        <NumberInput min={1} max={9999}
          onChange={val => this.updateBlurSetting('steps', val)}
          value={this.state.blurSettings.steps}></NumberInput>

        <ValueDragger deltaChange={delta => this.increaseSetting('stepScale', delta / 10, true, 1, 10)}>
          <label>Step Scale</label>
        </ValueDragger>
        <NumberInput onChange={val => this.updateBlurSetting('stepScale', val)}
          step={0.1} min={1} max={10}
          value={this.state.blurSettings.stepScale}></NumberInput>

        <ValueDragger deltaChange={delta => this.increaseSetting('gaussianBlur', delta, true, 0, 1000)}>
          <label>Gaussian Blur</label>
        </ValueDragger>
        <NumberInput suffix="px" min={0} max={1000}
          onChange={val => this.updateBlurSetting('gaussianBlur', val)}
          value={this.state.blurSettings.gaussianBlur}></NumberInput>


        <ValueDragger deltaChange={delta => this.increaseSetting('motionBlur', delta, true, 0, 2000)}>
          <label>Motion Blur</label>
        </ValueDragger>
        <NumberInput suffix="px" min={0} max={2000}
          onChange={val => this.updateBlurSetting('motionBlur', val)}
          value={this.state.blurSettings.motionBlur}></NumberInput>



      </div>
      <button className="apply" onClick={this.applyBlur}>Apply Blur</button>

    </section>
  }
  renderShadowSettings() {
    return <section className="tabContent">
      <div className="presetArea row">
        <div className="heading">Save / Load Preset</div>
        <div className="grid settings gap v-center row">
          <input type="text" className="presetName"
            onChange={e => this.updateShadowSetting('presetName', e.target.value)}
            placeholder="Preset Name"
            value={this.state.shadowSettings.presetName} />
          <button onClick={this.saveShadowPreset}
            className={this.state.justSaved ? 'justSaved' : ''}>
            {this.state.shadowPresets.includes(this.state.shadowSettings.presetName) ? 'Update' : 'Save'}
          </button>
        </div>
        <DropdownSelect hasAction ellipsis
          displayText="Load Preset"
          noOptionsText="no presets saved"
          options={this.state.shadowPresets}
          onAction={this.deleteShadowPreset}
          onSelect={this.loadShadowPreset}>
        </DropdownSelect>
      </div>
      <div className="heading">Adjust Settings</div>
      <div className="grid settings gap v-center row">
        <div className="flex angle-tuple">
          <ValueDragger deltaChange={delta => this.increaseSetting('angle', delta, false, -180, 180)}>
            <label>Angle</label>
          </ValueDragger>
          <RadialInput value={this.state.shadowSettings.angle}
            onChange={val => this.updateShadowSetting('angle', val)}>
          </RadialInput>
        </div>
        <NumberInput onChange={val => this.updateShadowSetting('angle', val)}
          value={this.state.shadowSettings.angle} min={-180} max={180} suffix="deg"></NumberInput>

        <ValueDragger deltaChange={delta => this.increaseSetting('distance', delta, false, 0, 9999)}>
          <label>Distance</label>
        </ValueDragger>
        <NumberUnitInput controlled
          onChange={uv => this.updateShadowSetting('distance', uv)}
          value={this.state.shadowSettings.distance}></NumberUnitInput>

        <ValueDragger deltaChange={delta => this.increaseSetting('steps', delta, false, 1, 9999)}>
          <label>Steps</label>
        </ValueDragger>
        <NumberInput min={1} max={9999}
          onChange={val => this.updateShadowSetting('steps', val)}
          value={this.state.shadowSettings.steps}></NumberInput>

        <ValueDragger deltaChange={delta => this.increaseSetting('stepScale', delta / 10, false, 1, 10)}>
          <label>Step Scale</label>
        </ValueDragger>
        <NumberInput min={1} max={10} step={0.1}
          onChange={val => this.updateShadowSetting('stepScale', val)}
          value={this.state.shadowSettings.stepScale}></NumberInput>

        <ValueDragger deltaChange={delta => this.increaseSetting('blur', delta, false, 0, 250)}>
          <label>Blur</label>
        </ValueDragger>
        <NumberInput suffix="px" min={0} max={250}
          onChange={val => this.updateShadowSetting('blur', val)}
          value={this.state.shadowSettings.blur}></NumberInput>

        <input type="checkbox" className="styled" name="cb_ugl" id="cb_flat"
          onChange={e => this.updateShadowSetting('flatten', e.target.checked)}
          checked={this.state.shadowSettings.flatten} />
        <label htmlFor="cb_flat">Flatten</label>

        <input type="checkbox" className="styled" name="cb_ugl" id="cb_ugl"
          onChange={e => this.updateShadowSetting('useGlobalLight', e.target.checked)}
          checked={this.state.shadowSettings.useGlobalLight} />
        <label htmlFor="cb_ugl">Glob. Light</label>

      </div>
      <button className="shadow apply" onClick={this.applyShadow}>Create Shadow</button>
    </section>
  }

  increaseSetting(key, amount, blurSetting, min, max) {
    const subState = blurSetting ? this.state.blurSettings : this.state.shadowSettings
    const newObj = increaseValue(subState[key], amount, min, max)
    const newSubState = { ...subState }
    newSubState[key] = newObj
    if (blurSetting) {
      this.setState({
        blurSettings: newSubState
      })
    } else {
      this.setState({
        shadowSettings: newSubState
      })
    }
  }

  deleteBlurPreset = (id) => {
    evalExtendscript(`eraseBlurPreset(${JSON.stringify(id)})`)
    const presetsWithout = removeFromArray(this.state.blurPresets, id)
    this.setState({ blurPresets: presetsWithout })
  }

  deleteShadowPreset = (id) => {
    evalExtendscript(`eraseShadowPreset(${JSON.stringify(id)})`)
    const presetsWithout = removeFromArray(this.state.shadowPresets, id)
    this.setState({ shadowPresets: presetsWithout })
  }

  loadBlurPreset = (id) => {
    evalExtendscript(`getBlurPresetSetting(${JSON.stringify(id)})`)
      .then(settings => {
        if (settings) {
          this.setState({ blurSettings: settings })
        }
      })
  }

  updateShadowSetting(key, newVal) {
    const shadowSettings = { ...this.state.shadowSettings }
    shadowSettings[key] = newVal
    this.setState({ shadowSettings: shadowSettings })
  }

  updateBlurSetting(key, newVal) {
    const blurSettings = { ...this.state.blurSettings }
    blurSettings[key] = newVal
    this.setState({ blurSettings: blurSettings })
  }

  showSavingUI() {
    this.setState({ justSaved: true })
    setTimeout(() => {
      this.setState({ justSaved: false })
    }, 1000)
  }

  saveShadowPreset = () => {
    evalExtendscript(`saveShadowPreset(${JSON.stringify(this.state.shadowSettings)})`)
    const presetName = this.state.shadowSettings.presetName || 'unnamed preset'
    if (!this.state.shadowPresets.includes(presetName)) {
      const shadowPresetsAdded = addToArray(this.state.shadowPresets, presetName)
      this.setState({ shadowPresets: shadowPresetsAdded })
    }
    this.showSavingUI()
  }

  saveBlurPreset = () => {
    evalExtendscript(`saveBlurPreset(${JSON.stringify(this.state.blurSettings)})`)
    const presetName = this.state.blurSettings.presetName || 'unnamed preset'
    if (!this.state.blurPresets.includes(presetName)) {
      const blurPresetsAdded = addToArray(this.state.blurPresets, presetName)
      this.setState({ blurPresets: blurPresetsAdded })
    }
    this.showSavingUI()
  }

  applyShadow = () => {
    evalExtendscript(`applyShadow(${JSON.stringify(this.state.shadowSettings)})`)
  }
  applyBlur = () => {
    evalExtendscript(`applyBlur(${JSON.stringify(this.state.blurSettings)})`)
  }

  componentDidUpdate() {
    this.saveLastSettings()
  }

  saveLastSettings = debounce(() => {
    if (window.__adobe_cep__ !== undefined) {
      evalExtendscript(`saveLastSettings(${JSON.stringify(this.state)})`)
    }
  }, 1000)
}

export default App;



