import React, { Component } from 'react';
import './ValueDragger.css'

class ValueDragger extends Component {

  lastPos

  render() {
    return (
      <div className="value-dragger"
        onMouseDown={e => this.mouseDownHandler(e.nativeEvent)}>
        { this.props.children }  
      </div>
    );
  }

  moveListener = (e) => {
    e.preventDefault()
    const delta = this.lastPos - e.pageY;
    this.lastPos = e.clientY
    if(delta !== 0 && this.props.deltaChange) {
      this.props.deltaChange(delta)
    }
  }

  mouseDownHandler(e) {
    this.lastPos = e.clientY
    const upListener = () => {
      if(this.props.dragEnd) {
        this.props.dragEnd()
      }
      window.removeEventListener('mousemove', this.moveListener)
      window.removeEventListener('mouseup', upListener)
    }
    window.addEventListener('mousemove', this.moveListener);
    window.addEventListener('mouseup', upListener);
  }

}

export default ValueDragger;


