import { trim } from './math'

export function addToArray(array,obj) {
  return [...array, obj]
}

export function removeFromArray(array, obj) {
  return array.filter((_obj) => obj !== _obj)
}

export function increaseValue(value, amount, min, max) {
  if (!isNaN(value)) {
    return fixDecimal(trim(value + amount, min, max))
  } else if (value instanceof Object) {
    const obj = value
    if(isNaN(obj.value)) {
      obj.value = 0
    }
    return {
      ...obj,
      value: trim(obj.value + amount, min, max)
    }
  } else {
    return trim(0 + amount, min, max)
  }
}

function fixDecimal(num) {
  if(countDecimals(num) > 0) {
    num = parseFloat(num.toFixed(1))
  }
  return num
}

var countDecimals = function (value) {
  if(Math.floor(value) === value) return 0;
  return value.toString().split(".")[1].length || 0; 
}


