import React, { Component } from 'react';

class RadialInput extends Component {

  size = 24
  radius = this.size / 2
  //startattop = true
  degrees = 0
  mousedown = false
  mouseenter = false
  boundCenter

  style = {
    fillStyle: 'transparent',
    strokeStyle: 'darkgrey',
    lineWidth: 2
  }

  render() {
    return (
      <canvas
        ref="canvas"
        height={this.size}
        width={this.size}
        onMouseDown={e => this.onMouseDown(e.nativeEvent)}>
      </canvas>
    );
  }

  componentDidUpdate() {
    this.set(this.convertDegBack(this.props.value))
  }

  componentDidMount() {
    this.setStyle(this.style);
    document.addEventListener('mousemove', this.onDocumentMouseMove)
    document.addEventListener('mouseup', this.onDocumentMouseUp)
    this.set(this.convertDegBack(this.props.value))
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.onDocumentMouseUp);
    document.removeEventListener('mousemove', this.onDocumentMouseMove);
  }

  onDocumentMouseUp = () => { this.mousedown = false }

  onMouseDown = (e) => {
    if (e.button) return

    var bounds = this.refs.canvas.getBoundingClientRect()
    this.boundCenter = {
      x: bounds.x + this.radius,
      y: bounds.y + this.radius
    }

    this.mousedown = true
    this.setDegreesByEvent(e)
  }

  onDocumentMouseMove = (e) => {
    if (this.mousedown) {
      e.stopPropagation()
      e.preventDefault()
      this.setDegreesByEvent(e);
    }
  }

  setDegreesByEvent(e) {

    var x = e.pageX - this.boundCenter.x,
      y = e.pageY - this.boundCenter.y,
      radian = Math.atan2(y, x),
      degree = Math.round(radian * 180 / Math.PI);
    
    if (degree < 0) {
      degree += 360;
    }

    if (this.startattop) {
      degree += 90;
      if (degree > 359) {
        degree -= 360;
      }
    }

    if (this.props.onChange) {
      this.props.onChange(this.convertDeg(degree))
    }
  }

  convertDeg(degrees) {
   if(degrees < 180) {
     return -degrees
   } else {
     return 360 - degrees
   }
  }

  convertDegBack(degrees) {
    if(degrees < 0) {
      return -degrees
    } else {
      return 360 - degrees
    }
  }

  convertDeg2(degrees) {
    if (degrees <= 180) {
      return 90 - degrees;
    } else {
      return 270 - degrees;
    }
  }

  convertDegBack2(degrees) {
    if (degrees <= 180) {
      return 90 - degrees;
    } else {
      return 270 - degrees;
    }
  }

  set(degrees) {
    this.degrees = Number(degrees || 0)
    if (this.startattop) {
      this.degrees -= 90
    }
    this.radian = this.degrees * Math.PI / 180
    this.updateCanvas()
  }

  get() {
    return this.input.value
  }

  setStyle(obj) {
    const context = this.refs.canvas.getContext('2d')
    obj = obj || {};
    for (var key in obj) {
      context[key] = obj[key];
    }
  }

  updateCanvas() {
    const context = this.refs.canvas.getContext('2d')
    const lineWidth = context.lineWidth
    const radius = this.radius, radian = this.radian
    const x = radius + radius * Math.cos(radian)
    const y = radius + radius * Math.sin(radian)
    // clear
    context.clearRect(0, 0, this.size, this.size);
    context.beginPath();
    // draw circle
    context.arc(radius, radius, radius - lineWidth / 2, 0, 2 * Math.PI);
    // draw radian
    context.moveTo(radius, radius);
    context.lineTo(x, y);

    context.fill();
    context.stroke();
  };

}

export default RadialInput;


