import React, { Component } from 'react';
import './SelectList.css'

class SelectList extends Component {

  renderList() {
   let listItems = []
   let liClass = this.props.hasAction ? 'hasAction ': ''
   this.props.options.forEach((opt,i) => {
      listItems.push(
      <li 
        title={opt}
        className={liClass + (this.isSelected(opt) ? 'selected' : '')}
        key={i} 
        onClick={() => this.onOptionClick(opt) }>
        {opt}
        {this.props.hasAction ? this.renderActionTrigger(opt) : null }
      </li>
      )
    })
    return <ul>{listItems}</ul>
  }

  renderActionTrigger(opt) {
    return <span className="actionTrigger"
      onClick={e => {
        e.stopPropagation()
        if(this.props.onAction) {
          this.props.onAction(opt)
        }
      }}
    ></span>
  }

  onOptionClick(option) {
    if(this.isSelected(option)) {
      if(this.props.multi && this.props.onDeselect) {
        this.props.onDeselect(option)
      }
    } else if(this.props.onSelect) {
      this.props.onSelect(option)
    }
  }

  isSelected(option) {
    const selectedOptions = this.props.selectedOptions || [this.props.selectedOption] || []
    return selectedOptions.includes(option)
  }

  noOptionsText() {
    return this.props.noOptionsText ? this.props.noOptionsText : 'no options available'
  }

  render() {
    return (
      <div className={"selectList" + (this.props.ellipsis ? ' ellipsis' : '') + (this.props.multi ? ' multi' : '')}>
        {this.props.options && this.props.options.length ? this.renderList() : this.noOptionsText()}
      </div>
    );
  }
}

export default SelectList;


