import React, { Component } from 'react';
import './NumberUnitInput.css';

/**
 * 1) Accepts multiple unit values like "10px 15% 3cm"
 * 2) onChange, returns an array of objects formatted like
 *    {value:..., unit:...}
 */
class NumberUnitInput extends Component {

  allowedUnits = ['px', 'mm', 'cm', 'in', '%', 'pt', 'pc']

  state = {
    text: '0px',
    valid : true
  }

  render() {
    return (
      <div className="NumberUnitInput input-wrapper">
        <input spellCheck="false" type="text" className={this.state.valid ? '': 'invalid'}
        value={this.state.text}
        onChange={e => this.onChange(e)}/>
      </div>
    );
  }

  onChange(e) {
    const text = e.target.value
    const uv = this.toUnitValue(text)
    this.setState({
      text: text,
      valid: this.isValid(uv)
    })

    if(this.props.onChange) {
      this.props.onChange(uv)
    }
  }

  toUnitValue(string) {
    const tuple = string.split(/([0-9]+)/).filter(Boolean)
    return {
      value: tuple[0],
      unit: tuple[1]
    }
  }

  toString(unitValueObject) {
    return (unitValueObject.value === 0 ? 0 : unitValueObject.value || '') +  (unitValueObject.unit || '')
  }

  unitValuesEqual(a, b) {
    return a.value === b.value && a.unit === b.unit
  }

  componentWillMount() {
    this.setStateFromProps(this.props)
  }

  setStateFromProps(props) {
    if(props.value) {
      this.setState({
        text: this.toString(props.value),
        valid: this.isValid(props.value)
      })
    }
  }

  componentWillReceiveProps(nextProps) {
    if(nextProps.controlled) {
      this.setStateFromProps(nextProps)
    }
  }


  /**
   * valid if the following 2 conditions are true 
   * 1) value is a number or unitvalue is empty
   * 2) unit is in allowedUnits or empty
   * @param {*} value 
   */
  isValid(unitValue) {
    const noValue = !unitValue.value
    const noUnit = !unitValue.unit
    const validNumber = !isNaN(unitValue.value)
    const validUnit = this.allowedUnits.indexOf(unitValue.unit) > -1
    
    return ((noValue && noUnit) || validNumber) &&
      (!unitValue.unit || validUnit)
  }
}

export default NumberUnitInput;


