import React, { Component } from 'react';
import './NumberInput.css';

class NumberInput extends Component {

  state = {
    hasFocus: false
  }

  render() {
    return (
      <div className="input-wrapper">
        <input
          id={this.props.id}
          disabled={this.props.disabled}
          onChange={e => this.validate(e.target.value)}
          onFocus={e => this.focus()}
          onBlur={e => this.blur()}
          type="number"
          step={this.props.step || 1}
          min={this.props.min}
          max={this.props.max}
          value={this.props.value} />
          {this.renderSuffix()}
        </div>
    );
  }

  renderSuffix() {
    if (this.props.suffix) {
      return <span className={"suffix " + (this.state.hasFocus ? 'hidden' : '')}>
        {this.props.suffix}
      </span>
    }
  }

  blur() {
    this.setState({
      hasFocus: false
    })
  }

  focus() {
    this.setState({
      hasFocus: true
    })
  }

  validate(value) {
    if (!isNaN(value)
      && (!this.props.min || value >= parseInt(this.props.min,10))
      && (!this.props.max || value <= parseInt(this.props.max,10))) {
      if(this.props.onChange) {
        this.props.onChange(parseFloat(value))
      }
    } else {
      // TODO: Display error
    }
  }
}

export default NumberInput;


