import React, { Component } from 'react';
import { Scrollbars } from 'react-custom-scrollbars';
import './Dropdown.css';

class Dropdown extends Component {

  node
  state = {
    extended: false
  }

  render() {
    return (
      <div className="dropdown" ref={node => this.node = node}>
        <div 
          className={"input-height toggle " + (this.state.extended ? 'extended ' : '') + (this.props.disabled ?'disabled' : '')}
          onClick={e => this.toggle()}>
            <span>{this.props.displayText || 'dropdown'}</span>
        </div>
        {this.state.extended && this.renderContent()}
      </div>
    );
  }

  renderContent() {
    return <div className="content-wrapper"><Scrollbars 
    autoHeight={true}
    renderTrackHorizontal={props => <div {...props} style={{display: 'none'}} className="track-horizontal"/>}
    renderTrackVertical={props => < div {...props} className="track-vertical"/>}
    renderThumbVertical={props => <div {...props} className="thumb-vertical"/>}>
      <div className="content">{this.getContent()}</div>
    </Scrollbars></div>
  }

  getContent() {
    return this.props.children
  }

  handleClick = (e) => {
    if(this.node.contains(e.target)) {
      return
    } 
    this.handleClickOutside()
  }

  handleClickOutside() {
    this.collapse()
  }

  extend() {
    document.addEventListener('mousedown', this.handleClick, false)
    this.setState({
      extended: true
    })
  }

  collapse() {
    document.removeEventListener('mousedown', this.handleClick, false)
    this.setState({
      extended: false
    })
  }

  toggle() {
    if(this.state.extended) {
      this.collapse()
    } else {
      this.extend()
    }
  }

}

export default Dropdown;


